import Link from "next/link";
import Countdown from "../components/Countdown";

export default async function DealsPage() {
  const res = await fetch("http://localhost:3000/api/deals", { cache: "no-store" });
  const deals = await res.json();

  const productImages: Record<string, string> = {
    "Wireless Headphones": "https://images.unsplash.com/photo-1580894732444-8ec1b9f6f7f4",
    "Smartwatch": "https://images.unsplash.com/photo-1516574187841-cb9cc2ca948b",
    "Bluetooth Speaker": "https://images.unsplash.com/photo-1585386959984-a4155223f9d4",
    "Laptop Stand": "https://images.unsplash.com/photo-1616627457119-2f6a1a3f6a2e"
  };

  return (
    <main className="min-h-screen bg-slate-950 p-8">
      <h1 className="text-3xl font-bold text-white mb-8">Available Deals</h1>

      <div className="mb-8 rounded-2xl bg-slate-800 p-6 shadow">
        <p className="text-sm uppercase tracking-[0.4em] text-slate-400 mb-2">Deals Countdown</p>
        <Countdown targetDate="2025-12-13T02:00:00" />
      </div>

      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {deals.map((deal: any) => {
          // Create a slug from product name
          const slug = deal.product.toLowerCase().replace(/\s+/g, "-");
          return (
            <div
              key={deal.id}
              className="rounded-lg bg-slate-800 p-6 shadow hover:shadow-lg transition flex flex-col items-center"
            >
              <img
                src={productImages[deal.product] || "https://via.placeholder.com/150"}
                alt={deal.product}
                className="mb-4 rounded-md h-40 w-auto object-cover"
              />

              <h2 className="text-xl font-semibold text-white mb-2">{deal.product}</h2>
              <p className="text-slate-300 mb-1">${deal.price}</p>
              <p className="text-green-400 font-medium mb-4">{deal.discount} off</p>

              {/* Unique Shop Now link */}
              <Link
                href={`/shop/${slug}`}
                className="rounded-full bg-white px-4 py-2 text-sm font-semibold text-slate-950 shadow hover:shadow-lg transition"
              >
                Shop Now
              </Link>
            </div>
          );
        })}
      </div>
    </main>
  );
}